@import '~src/styles/customized-bootstrap';

.cs-frame {
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    z-index: 0;

    &.blur {
        background: rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(10px);
    }

    .cs-frame-component-container {

        background: $black;

        &.cs-frame-cherryeditor {
            z-index: 2;
            border-color: $cyan !important;
        }

        // terminal
        &.cs-frame-cherryterm {
            z-index: 3;
            border-color: $gray-900 !important;

            .ap-wrapper { // override asciinema css
                align-items: center;
            }
        }

        // screen
        &.cs-frame-cherryscreen {
            z-index: 4;
            border-color: $blue !important;
        }

        // Camera: only one camera component to be displayed
        &.cs-frame-cherrycam {
            z-index: 5;
            border-color: $red !important;
        }

        // note
        &.cs-frame-cherrynote {
            z-index: 6;
            border-color: $green !important;
            background: transparent;
        }

        video {
            width: 100%;
            height: 100%;
            max-width: 100%;
            max-height: 100%;
            object-position: center;
            object-fit: contain;
        }
    }
}
