// Override default variables before the import
// $body-bg: #000;

// Import Bootstrap and its default variables

$dark-blue: #24285b;
$blue: #0d6efd;
$red: #dc3545;
$yellow: #ffc107;
$green: #198754;
$cyan: #0dcaf0;
$black: #000;
$gray: #6c757d;
$colors: (
      "blue":       $blue,
      "red":        $red,
      "yellow":     $yellow,
      "green":      $green,
      "cyan":       $cyan,
      "black":      $black,
      "gray":       $gray,
);

@mixin generate-bg-classes() {
  @each $color, $value in $colors {
    .bg-#{$color} {
      background-color: $value !important;
    }
  }
}

@include generate-bg-classes();


$primary: $dark-blue;
$secondary: $yellow;
$info: $cyan;
$success: $green;
$warning: $yellow;
$danger: $red;
$body-bg: #FFFFFF;
$body-color: #333333;
$light: #f5f6f8;

// Import Bootstrap
@import "~bootstrap/scss/bootstrap";


