.cs-pointer {
  --z-index: 3;
  --height: 0px;
  --percentage: 0;
  --rotate: 0deg;
  position: absolute;
  left: calc(var(--pos, 0%));
  bottom: 0;
  z-index: var(--z-index);
  height: var(--height);
  width: 2px;
  background-color: black;
  transform-origin: bottom;
  transform: rotate(var(--rotate));

  .square {
    --reverse-rotate: calc(-1 * var(--rotate));
    position: absolute;
    bottom: 0;
    left: -14px;
    display: flex;
    align-items: end;
    justify-content: end;
    transform: rotate(var(--reverse-rotate));

    &:after {
      content: "";
      border-width: 1px;
      border-style: dashed;
      border-color: black;
      width: calc(var(--percentage) * 100vw / 100);
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  &.disabled {
    opacity: 0.3;
  }

  &.cs-cherryterm-color .square:after {
    border-color: black;
  }
  &.cs-cherrynote-color .square:after {
    border-color: green;
  }
  &.cs-cherryscreen-color .square:after {
    border-color: blue;
  }
  &.cs-cherrycam-color .square:after {
    border-color: red;
  }
  &.cs-cherrymediafile-color .square:after {
    border-color: red;
  }
  &.cs-cherryeditor-color .square:after {
    border-color: cyan;
  }

  &.left {
    --rotate: -45deg;
  }

  &.right {
    --rotate: 45deg;
  }
}