  display: block;
  flex: 0 0 auto;
  cursor: pointer;
  height: 12px;
  width: 12px;
  padding: 10px;
}
div.ap-control-bar span.ap-playback-button svg {
  height: 12px;
  width: 12px;
}
div.ap-control-bar span.ap-timer {
  display: block;
  flex: 0 0 auto;
  min-width: 50px;
  margin: 0 10px;
  height: 100%;
  text-align: center;
  font-size: 11px;
  line-height: 34px;
  cursor: default;
}
div.ap-control-bar span.ap-timer span {
  display: inline-block;
  font-size: inherit;
}
