.cs-transparent-hollow-overlay {
  --circle-size: 60%;
  position: relative;
  margin:0 auto;
  overflow: hidden; /* Clips the content inside the circle */

  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: var(--circle-size);
    padding-top: var(--circle-size);
    box-shadow: 0 0 0 2000px rgba(0, 0, 0, 0.5); /* This color makes the hole transparent */
    border-radius: 50%; /* This creates a circular hole */
  }
}